import { factory } from '../../utils/factory.ts'
import { isCollection } from '../../utils/is.ts'
import { wasmLoader } from '../../wasm/WasmLoader.ts'
import type { TypedFunction } from '../../core/function/typed.ts'

// Minimum array length for WASM to be beneficial
const WASM_STD_THRESHOLD = 100

/**
 * Check if an array is a flat array of plain numbers
 */
function isFlatNumberArray(arr: unknown[]): arr is number[] {
  for (let i = 0; i < arr.length; i++) {
    if (typeof arr[i] !== 'number') {
      return false
    }
  }
  return true
}

// Type definitions for std
interface MatrixType {
  valueOf(): unknown[] | unknown[][]
}

interface StdDependencies {
  typed: TypedFunction
  map: TypedFunction
  sqrt: TypedFunction
  variance: TypedFunction
}

type NormalizationType = 'unbiased' | 'uncorrected' | 'biased'

const name = 'std'
const dependencies = ['typed', 'map', 'sqrt', 'variance']

export const createStd = /* #__PURE__ */ factory(
  name,
  dependencies,
  ({ typed, map, sqrt, variance }: StdDependencies) => {
    /**
     * Compute the standard deviation of a matrix or a  list with values.
     * The standard deviations is defined as the square root of the variance:
     * `std(A) = sqrt(variance(A))`.
     * In case of a (multi dimensional) array or matrix, the standard deviation
     * over all elements will be calculated by default, unless an axis is specified
     * in which case the standard deviation will be computed along that axis.
     *
     * Additionally, it is possible to compute the standard deviation along the rows
     * or columns of a matrix by specifying the dimension as the second argument.
     *
     * Optionally, the type of normalization can be specified as the final
     * parameter. The parameter `normalization` can be one of the following values:
     *
     * - 'unbiased' (default) The sum of squared errors is divided by (n - 1)
     * - 'uncorrected'        The sum of squared errors is divided by n
     * - 'biased'             The sum of squared errors is divided by (n + 1)
     *
     *
     * Syntax:
     *
     *     math.std(a, b, c, ...)
     *     math.std(A)
     *     math.std(A, normalization)
     *     math.std(A, dimension)
     *     math.std(A, dimension, normalization)
     *
     * Examples:
     *
     *     math.std(2, 4, 6)                     // returns 2
     *     math.std([2, 4, 6, 8])                // returns 2.581988897471611
     *     math.std([2, 4, 6, 8], 'uncorrected') // returns 2.23606797749979
     *     math.std([2, 4, 6, 8], 'biased')      // returns 2
     *
     *     math.std([[1, 2, 3], [4, 5, 6]])      // returns 1.8708286933869707
     *     math.std([[1, 2, 3], [4, 6, 8]], 0)    // returns [2.1213203435596424, 2.8284271247461903, 3.5355339059327378]
     *     math.std([[1, 2, 3], [4, 6, 8]], 1)    // returns [1, 2]
     *     math.std([[1, 2, 3], [4, 6, 8]], 1, 'biased') // returns [0.7071067811865476, 1.4142135623730951]
     *
     * See also:
     *
     *    mean, median, max, min, prod, sum, variance
     *
     * @param {Array | Matrix} array
     *                        A single matrix or or multiple scalar values
     * @param {string} [normalization='unbiased']
     *                        Determines how to normalize the variance.
     *                        Choose 'unbiased' (default), 'uncorrected', or 'biased'.
     * @param dimension {number | BigNumber}
     *                        Determines the axis to compute the standard deviation for a matrix
     * @return {*} The standard deviation
     */
    return typed(name, {
      // std([a, b, c, d, ...])
      'Array | Matrix': _std,

      // std([a, b, c, d, ...], normalization)
      'Array | Matrix, string': _std,

      // std([a, b, c, c, ...], dim)
      'Array | Matrix, number | BigNumber': _std,

      // std([a, b, c, c, ...], dim, normalization)
      'Array | Matrix, number | BigNumber, string': _std,

      // std(a, b, c, d, ...)
      '...': function (args: unknown[]): unknown {
        return _std(args)
      }
    })

    function _std(
      array: unknown[] | MatrixType,
      normalizationOrDim?: NormalizationType | number | { valueOf(): number }
    ): unknown {
      if ((array as unknown[]).length === 0) {
        throw new SyntaxError(
          'Function std requires one or more parameters (0 provided)'
        )
      }

      // WASM fast path for flat arrays of plain numbers with normalization (not dimension)
      // Only use WASM when we have a flat array and string normalization (or default)
      const normalization: NormalizationType =
        typeof normalizationOrDim === 'string'
          ? normalizationOrDim
          : 'unbiased'
      const isDimension = typeof normalizationOrDim === 'number' ||
        (normalizationOrDim !== undefined && typeof normalizationOrDim === 'object')

      if (
        !isDimension &&
        Array.isArray(array) &&
        array.length >= WASM_STD_THRESHOLD &&
        (normalization === 'unbiased' || normalization === 'uncorrected')
      ) {
        if (isFlatNumberArray(array)) {
          const wasm = wasmLoader.getModule()
          if (wasm) {
            try {
              const alloc = wasmLoader.allocateFloat64Array(array)
              try {
                const ddof = normalization === 'unbiased' ? 1 : 0
                return wasm.statsStd(alloc.ptr, array.length, ddof)
              } finally {
                wasmLoader.free(alloc.ptr)
              }
            } catch {
              // Fall back to JS implementation on WASM error
            }
          }
        }
      }

      // JavaScript fallback
      try {
        const v = variance.apply(null, arguments as unknown as unknown[])
        if (isCollection(v)) {
          return map(v, sqrt)
        } else {
          return sqrt(v)
        }
      } catch (err: unknown) {
        if (err instanceof TypeError && err.message.includes(' variance')) {
          throw new TypeError(err.message.replace(' variance', ' std'))
        } else {
          throw err
        }
      }
    }
  }
)
